.file-upload-container {
  /* 
  padding: 20px;
  border: 2px dashed #007bff;
  border-radius: 8px; */
  background-color: #f8f9fa;
  text-align: center;
  position: relative;
  height: calc(100vh - 102px);
  border: 2px dashed #007bff;
  padding-top: 10px;
  border-radius: 5px;
}
.file-upload-container h2 {
  position: relative;
  z-index: 1;
}
.file-list {
  position: absolute;
  bottom: 0;
}

.file-upload-container label {
  border: none;
  height: 100%;
  max-width: 100%;
  min-width: 100%;
  height: 100%;
  padding: 20px;
  justify-content: center;
}
.file-upload-container label div {
  flex-grow: 0;
}
.file-types {
  display: none;
}
.upload-area {
  width: 100%;
  background: #f8f9fa;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
